.top-achievers {
  text-align: center;
  padding: 50px 20px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.main-title {
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 10px;
  color: white;
}

.subtitle {
  font-size: 1.1rem;
  color: wheat;
  margin-bottom: 40px;
}

.achievers-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
}

.achiever-card {
  background: #111827;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  max-width: 320px;
  color: #fff;
  width: 100%;
  padding: 20px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.achiever-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 6px 15px rgba(0,0,0,0.15);
}

.achiever-image {
  width: 100%;
  height: auto;
  border-radius: 10px;
  margin-bottom: 15px;
  display: block;
}

.achiever-title {
  font-size: 1.4rem;
  font-weight: 700;
  color: white;
  margin-bottom: 10px;
}

.achiever-desc {
  font-size: 0.95rem;
  color: white;
  line-height: 1.55;
  margin-bottom: 16px;
}

.achiever-btn {
  padding: 10px 20px;
  background-color:#102d6dff;
  color: #fff;
  font-weight: 700;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  text-decoration: none;
}

.achiever-btn:hover {
  background-color: #5b21b6;
}

/* Responsive */
@media (max-width: 768px) {
  .achievers-grid {
    flex-direction: column;
    align-items: center;
  }
}
